.work-container{
    color: rgb(188,180,180);
    display: flex;
    flex-direction:column ;
    align-items: center;
    margin-top: 5rem;
}
.work-header{
    font-size: 1.2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.work-header-caps{
    font-weight: bolder;
    font-size: xx-large;
}
.exp-changer-container{
    display: flex;
    flex-direction: row;
    margin-block: 1.5rem;
    
}
.left-arrow{
    background-color: rgb(30,30,30);
    padding-block: 0.3rem;
    padding-inline: 1rem;
    border-radius: 0.5rem;
    margin-inline: 1rem;
}
.right-arrow{
    background-color: rgb(30,30,30);
    padding-block: 0.3rem;
    padding-inline: 1rem;
    border-radius: 0.5rem;
    margin-inline: 1rem;
}
.review-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 40rem;
    text-align: center;
    background-color: rgb(30,30,30);
    border-radius: 10px;
    padding-inline: 7rem;
    padding-top: 1rem;
    padding-bottom: 2rem;
    margin-bottom: 2rem;
    
}
.animate{
    animation-name: popup;
    animation-duration: 1.2s;
}
.review-container::before{
    background-color: rgb(30,30,30);
}

@keyframes popup {
    from {
        opacity: 0;
        width: 20rem;
        
        /* max-height: 1rem; */
    
    }
    to {
        opacity: 1;
        width: 40rem;
        
    /* max-width: fit-content; */
}
  }
.work-heading{
    font-weight: bolder;
    font-size: 2.2rem;
}
.work-subscript{
    /* font-weight: bolder; */
    font-size: 1.2rem;
    /* text-decoration: underline; */
}
.work-about{
    margin-top: 1.5rem;
    font-size: 1.1rem;
}
.work-tech-heading{
    margin-top: 1.5rem;
    font-size: 1.2rem;
    font-weight: bolder;
    margin-bottom: .5rem;
}